export const wxShareConfig = function () {
    this.http.get("").then(res => {
        wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"]
        });
    });
    wx.ready(function () {
        //  JS 
        wx.checkJsApi({
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
            success: function (res) {
                console.log(JSON.stringify(res));
            }
        });
        // 
        wx.onMenuShareTimeline({
            title: "",
            link: location.href,
            imgUrl: "https://niuzhigongzuo.com/static/images/logo@x1024.jpg",
            success: function () {
                alertStr("");
            },
            cancel: function () {
                alertStr("");
            },
            fail: function (res) {
                console.log(JSON.stringify(res));
                alertStr("");
            }
        });

        // 
        wx.onMenuShareAppMessage({
            title: "",
            desc: "",
            link: location.href,
            imgUrl: "https://niuzhigongzuo.com/static/images/logo@x1024.jpg",
            type: "link",
            success: function () {
                alertStr("");
            },
            cancel: function () {
                alertStr("");
            },
            fail: function (res) {
                console.log(JSON.stringify(res));
                alertStr("");
            }
        });
    });
}

export const wxhideMenu = function () {
    this.http.get("").then(res => {
        wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"]
        });
    });
    wx.ready(function () {
        //  JS 
        wx.checkJsApi({
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
            success: function (res) {
                console.log(JSON.stringify(res));
            }
        });
        wx.hideMenuItems({
            menuList: ["menuItem:share:timeline", "menuItem:copyUrl", "menuItem:share:appMessage", "menuItem:share:qq", "menuItem:share:weiboApp", "menuItem:favorite", "menuItem:share:facebook", "menuItem:share:QZone", "menuItem:editTag", "menuItem:delete", "menuItem:copyUrl", "menuItem:originPage", "menuItem:readMode", "menuItem:openWithQQBrowser", "menuItem:openWithSafari", "menuItem:share:email", "menuItem:share:brand"] // 
        });
    });
}


